:root {
  --dark-navy: #0a192f;
  --navy: #0a192f;
  --light-navy: #112240;
  --lightest-navy: #233554;
  --slate: #8892b0;
  --light-slate: #a8b2d1;
  --lightest-slate: #ccd6f6;
  --white: #e6f1ff;
  --green: #64ffda;

  --background: var(--navy);
  --foreground: var(--slate);

  --card: var(--light-navy);
  --card-foreground: var(--light-slate);

  --popover: var(--light-navy);
  --popover-foreground: var(--light-slate);

  --primary: var(--green);
  --primary-foreground: var(--navy);

  --secondary: var(--lightest-navy);
  --secondary-foreground: var(--lightest-slate);

  --muted: var(--light-navy);
  --muted-foreground: var(--light-slate);

  --accent: var(--green);
  --accent-foreground: var(--navy);

  --destructive: hsl(0 62.8% 30.6%);
  --destructive-foreground: var(--white);

  --border: var(--lightest-navy);
  --input: var(--lightest-navy);
  --ring: var(--green);

  --radius: 0.5rem;

  --transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);

  --font-mono: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  margin: 0;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background-color: var(--background);
  color: var(--foreground);
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
  font-size: 18px;
  line-height: 1.3;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 10px 0;
  font-weight: 600;
  color: var(--lightest-slate);
  line-height: 1.1;
}

.big-heading {
  margin: 0;
  font-size: clamp(40px, 8vw, 80px);
}

.medium-heading {
  margin: 0;
  font-size: clamp(40px, 8vw, 60px);
}

section {
  margin: 0 auto;
  padding: 100px 0;
  max-width: 1000px;
}

@media (max-width: 768px) {
  section {
    padding: 80px 0;
  }
}

@media (max-width: 480px) {
  section {
    padding: 60px 0;
  }
}

img,
svg {
  max-width: 100%;
  vertical-align: middle;
}

a {
  display: inline-block;
  text-decoration: none;
  text-decoration-skip-ink: auto;
  color: inherit;
  position: relative;
  transition: var(--transition);
}

a:hover,
a:focus {
  color: var(--green);
}

.numbered-heading {
  display: flex;
  align-items: center;
  position: relative;
  margin: 10px 0 40px;
  width: 100%;
  font-size: clamp(26px, 5vw, 32px);
  white-space: nowrap;
}

.numbered-heading:before {
  position: relative;
  bottom: 4px;
  counter-increment: section;
  content: '0' counter(section) '.';
  margin-right: 10px;
  color: var(--green);
  font-family: var(--font-mono);
  font-size: clamp(16px, 3vw, 20px);
  font-weight: 400;
}

.numbered-heading:after {
  content: '';
  display: block;
  position: relative;
  top: -5px;
  width: 300px;
  height: 1px;
  margin-left: 20px;
  background-color: var(--lightest-navy);
}

.before\:absolute::before {
  content: var(--tw-content);
  position: absolute;
}

.before\:left-0::before {
  content: var(--tw-content);
  left: 0px;
}

.before\:text-green::before {
  content: var(--tw-content);
  --tw-text-opacity: 1;
  color: rgb(100 255 218 / var(--tw-text-opacity, 1));
}

.before\:content-\[\'\25B9\'\]::before {
  --tw-content: '▹';
  content: var(--tw-content);
}

.after\:mx-auto::after {
  content: var(--tw-content);
  margin-left: auto;
  margin-right: auto;
}

.after\:my-0::after {
  content: var(--tw-content);
  margin-top: 0px;
  margin-bottom: 0px;
}

.after\:block::after {
  content: var(--tw-content);
  display: block;
}

.after\:h-\[90px\]::after {
  content: var(--tw-content);
  height: 90px;
}

.after\:w-\[1px\]::after {
  content: var(--tw-content);
  width: 1px;
}

.after\:bg-light-slate::after {
  content: var(--tw-content);
  --tw-bg-opacity: 1;
  background-color: rgb(168 178 209 / var(--tw-bg-opacity, 1));
}

.after\:transition-all::after {
  content: var(--tw-content);
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.after\:duration-200::after {
  content: var(--tw-content);
  transition-duration: 200ms;
}

.after\:content-\[\'\'\]::after {
  --tw-content: '';
  content: var(--tw-content);
}

.after\:duration-200::after {
  content: var(--tw-content);
  animation-duration: 200ms;
}

.hover\:-translate-y-1:hover {
  --tw-translate-y: -0.25rem;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:bg-green:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(100 255 218 / var(--tw-bg-opacity, 1));
}

.hover\:bg-green\/10:hover {
  background-color: rgb(100 255 218 / 0.1);
}

.hover\:bg-light-navy\/20:hover {
  background-color: rgb(17 34 64 / 0.2);
}

.hover\:bg-light-navy\/30:hover {
  background-color: rgb(17 34 64 / 0.3);
}

.hover\:bg-transparent:hover {
  background-color: transparent;
}

.hover\:bg-opacity-10:hover {
  --tw-bg-opacity: 0.1;
}

.hover\:text-green:hover {
  --tw-text-opacity: 1;
  color: rgb(100 255 218 / var(--tw-text-opacity, 1));
}

.hover\:underline:hover {
  text-decoration-line: underline;
}

.hover\:mix-blend-normal:hover {
  mix-blend-mode: normal;
}

.hover\:shadow-xl:hover {
  --tw-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 20px 25px -5px var(--tw-shadow-color), 0 8px 10px -6px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.hover\:grayscale-0:hover {
  --tw-grayscale: grayscale(0);
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}

.hover\:after\:w-full:hover::after {
  content: var(--tw-content);
  width: 100%;
}

@media (min-width: 768px) {

  .md\:col-span-7 {
    grid-column: span 7 / span 7;
  }

  .md\:col-start-6 {
    grid-column-start: 6;
  }

  .md\:mx-0 {
    margin-left: 0px;
    margin-right: 0px;
  }

  .md\:mt-0 {
    margin-top: 0px;
  }

  .md\:block {
    display: block;
  }

  .md\:flex {
    display: flex;
  }

  .md\:table-cell {
    display: table-cell;
  }

  .md\:hidden {
    display: none;
  }

  .md\:h-\[50px\] {
    height: 50px;
  }

  .md\:w-\[160px\] {
    width: 160px;
  }

  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .md\:flex-row {
    flex-direction: row;
  }

  .md\:flex-col {
    flex-direction: column;
  }

  .md\:flex-nowrap {
    flex-wrap: nowrap;
  }

  .md\:justify-end {
    justify-content: flex-end;
  }

  .md\:gap-12 {
    gap: 3rem;
  }

  .md\:overflow-x-visible {
    overflow-x: visible;
  }

  .md\:border-b-0 {
    border-bottom-width: 0px;
  }

  .md\:border-l-2 {
    border-left-width: 2px;
  }

  .md\:px-\[150px\] {
    padding-left: 150px;
    padding-right: 150px;
  }

  .md\:px-\[50px\] {
    padding-left: 50px;
    padding-right: 50px;
  }

  .md\:text-left {
    text-align: left;
  }

  .md\:text-right {
    text-align: right;
  }
}

@media (min-width: 1024px) {

  .lg\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
